import React, { Component } from 'react';

import MyCard from './Card';


export default class MyList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cards: [],
      url: props.url
    }




  }

  shouldComponentUpdate(nextProps, nextState) {

    if((nextProps.url) && (nextProps.url !== this.state.url) || (this.state.cards.length === 0)) {

    return true;
  }
    return false;

  }



  render() {

    let url = this.props.url;
    if(url) {
      url = '?categories=' + url;
    }
    else {
      url = '';
    }
    let list;
    fetch(`${process.env.REACT_APP_URL}/api/test${url}`,{//public-api/v1.4/events/?lang=ru&fields=dates,short_title,images,title,description,id&expand=dates&location=nsk&actual_since=1444385206&actual_until=1644385405&is_free=true`, { //https://justgonskapitest.azurewebsites.net    ${process.env.REACT_APP_URL}/api/Test
      mode: 'cors'
    }).then(res => {
      return res.json()
    }).then(val => {
      list = val.results;
      console.log(list);
      let i = 0;
      let cards = list.map((val) => {
        return <MyCard
                  key={i++}
                  id={val.id}
                  src={val.images[0].image}
                  title={val.title.toUpperCase()}
                  date={val.dates[0].start_Date ? val.dates[0].start_Date.replace(/-/g, '.').replace(/T/g, '   '): 'Идёт круглый год'}
                  description={val.description.replace('<p>', '').replace('</p>', '')}
                />
      })
      this.setState({cards: cards, url: this.props.url});
    });

    return (
      <div className="wrapper">
      { this.state.cards || <MyCard />}
      </div>
    )
  }
}
